import React from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router';
import { Panel, PanelHeader, HeaderButton } from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import FilterableList from '../organisms/FilterableList';

const CitySelector = ({ id, items, history, onSelect }) => (
  <Panel id={id}>
    <PanelHeader
      left={
        <HeaderButton onClick={() => history.goBack()}>
          <Icon28ChevronBack />
        </HeaderButton>
      }
    >
      Выбор города
    </PanelHeader>
    <FilterableList items={items} onSelect={onSelect} />
  </Panel>
);

CitySelector.propTypes = {
  id: PropTypes.string.isRequired,
  items: PropTypes.arrayOf(PropTypes.object).isRequired,
  onSelect: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired
};

export default withRouter(CitySelector);
