/* globals window, document */
import React from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router';
import {
  Panel,
  PanelHeader,
  Spinner,
  HeaderButton,
  Footer
} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import uuid from 'uuid';

import styles from './Search.module.css';
import DoctorCard from '../organisms/DoctorCard';

class SearchResults extends React.Component {
  componentWillMount() {
    window.addEventListener('scroll', this.onScroll, false);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.onScroll, false);
  }

  onScroll = () => {
    const {
      loadMore,
      search: { isLoading, hasMore }
    } = this.props;
    if (isLoading || !hasMore) {
      return;
    }
    if (
      window.innerHeight + window.scrollY >=
      document.body.scrollHeight - 800
    ) {
      loadMore();
    }
  };

  render() {
    const {
      id,
      search: { title, results, isLoading, description, hasMore },
      history,
      query: { date },
      onDoctorSelected
    } = this.props;

    return (
      <Panel id={id} className={styles.Search}>
        <PanelHeader
          left={
            <HeaderButton onClick={() => history.goBack()}>
              <Icon28ChevronBack />
            </HeaderButton>
          }
        >
          {title}
        </PanelHeader>
        <div className={styles.Description}>
          Расписание варчей на{' '}
          <span className={styles.SearchParams}>{description}</span>
        </div>
        <div className={styles.Results}>
          {results.map(doctor => (
            <div className={styles.Doctor} key={uuid() + doctor.Id}>
              <DoctorCard
                className={styles.DoctorCard}
                doctor={doctor}
                date={date}
                onClick={() => onDoctorSelected(doctor)}
              />
            </div>
          ))}
        </div>
        <Footer>
          {isLoading && <Spinner />}
          {!isLoading && !results.length && (
            <div>К сожалению, по вашему запросу ничего не найдено.</div>
          )}
          {!isLoading && results.length > 0 && !hasMore && (
            <div>
              Найдено
              {` ${results.length} `}
              врачей
            </div>
          )}
        </Footer>
      </Panel>
    );
  }
}

SearchResults.propTypes = {
  id: PropTypes.string.isRequired,
  history: PropTypes.object.isRequired,
  loadMore: PropTypes.func.isRequired,
  search: PropTypes.shape({
    title: PropTypes.string,
    results: PropTypes.arrayOf(PropTypes.object).isRequired,
    isLoading: PropTypes.bool.isRequired,
    description: PropTypes.string.isRequired,
    hasMore: PropTypes.bool.isRequired
  }).isRequired,
  query: PropTypes.shape({
    date: PropTypes.string
  }).isRequired,
  onDoctorSelected: PropTypes.func.isRequired
};

export default withRouter(SearchResults);
