/* eslint global-require: "off" */

import { all, call, fork } from 'redux-saga/effects';

export default function* rootSaga() {
  yield call(require('./initApp').default);
  yield all([
    fork(require('./search').watchSearch),
    fork(require('./loadSpecialities').watchLoadSpecialities),
    fork(require('./loadStations').watchLoadStations),
    fork(require('./loadDoctor').watchLoadDoctor)
  ]);
}
