import { connect } from 'react-redux';
import { withRouter } from 'react-router';

import actions from '../../actions';
import Doctor from '../../components/panels/Doctor';
import { getSelectedDoctor, getSearchParams } from '../../selectors';

const mapStateToProps = state => ({
  doctor: getSelectedDoctor(state).doctor,
  achievements: getSelectedDoctor(state).doctor.Achievements,
  reviews: getSelectedDoctor(state).doctor.Reviews,
  isLoading: getSelectedDoctor(state).isLoading,
  date: getSearchParams(state).date,
  description: getSelectedDoctor(state).doctor.TextSpec,
  title: getSearchParams(state).speciality.Name
});

const mapDispatchToProps = dispatch => ({
  load: id => {
    dispatch({
      type: actions.LOAD_DOCTOR,
      payload: { id }
    });
  }
});

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(Doctor)
);
